.g-ft {
  background-color: #666464;
  overflow: hidden;
  min-width: 1200px;
  padding-bottom: 50px;
}
.g-ft .footer-content {
  margin: 0 auto;
  padding-bottom: 105px;
  width: 1506px;
  height: 100%;
  overflow: hidden;
  border-bottom: solid 1px #c8caca;
}
.g-ft .footer-content .tel {
  width: 230px;
  height: 40px;
  background-image: url('../../images/tel.png');
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: 100% 100%;
          background-size: 100% 100%;
}
.g-ft .footer-content .index-button {
  margin-top: 20px;
  text-align: center;
  height: 50px;
  line-height: 50px;
  background-color: #7e318e;
  -webkit-border-radius: 3px;
          border-radius: 3px;
  font-size: 20px;
  font-weight: bold;
}
.g-ft .footer-content .ft-fl,
.g-ft .footer-content .ft-fr {
  margin-top: 60px;
}
.g-ft .footer-content .ft-fl li,
.g-ft .footer-content .ft-fr li {
  float: left;
  color: #fff;
  margin-right: 100px;
}
.g-ft .footer-content .ft-fl li h4,
.g-ft .footer-content .ft-fr li h4 {
  margin-bottom: 45px;
  font-weight: bold;
  font-size: 21px;
}
.g-ft .footer-content .ft-fl li a,
.g-ft .footer-content .ft-fr li a {
  margin-bottom: 10px;
  display: block;
  color: #c8caca;
  font-size: 18px;
}
.g-ft .footer-content .ft-fl li p,
.g-ft .footer-content .ft-fr li p {
  color: #c8caca;
  margin-bottom: 10px;
  font-size: 18px;
}
.g-ft .footer-content .ft-fl li .campus-link,
.g-ft .footer-content .ft-fr li .campus-link {
  position: relative;
}
.g-ft .footer-content .ft-fl li .campus-link .campus-list,
.g-ft .footer-content .ft-fr li .campus-link .campus-list {
  display: none;
  position: absolute;
  width: 300px;
  height: 200px;
  left: 130px;
  top: -90px;
  background-color: #bc97c2;
  -webkit-border-radius: 5px;
          border-radius: 5px;
}
.g-ft .footer-content .ft-fl li .campus-link .campus-list .campus-list-item,
.g-ft .footer-content .ft-fr li .campus-link .campus-list .campus-list-item {
  width: 100%;
  height: 100px;
  padding: 10px;
}
.g-ft .footer-content .ft-fl li .campus-link .campus-list .campus-list-item .campus-name p,
.g-ft .footer-content .ft-fr li .campus-link .campus-list .campus-list-item .campus-name p {
  color: #fff;
  font-size: 12px;
  font-weight: bold;
}
.g-ft .footer-content .ft-fl li .campus-link .campus-list .campus-list-item .campus-detail p,
.g-ft .footer-content .ft-fr li .campus-link .campus-list .campus-list-item .campus-detail p {
  color: #fff;
  font-size: 8px;
}
.g-ft .footer-content .ft-fl li .campus-link a,
.g-ft .footer-content .ft-fr li .campus-link a {
  position: relative;
  float: left;
}
.g-ft .footer-content .ft-fl li .campus-link a img,
.g-ft .footer-content .ft-fr li .campus-link a img {
  width: 7px;
  height: 14px;
  position: absolute;
  top: 7px;
  left: 5px;
}
.g-ft .footer-content .ft-fl li .recruit-item,
.g-ft .footer-content .ft-fr li .recruit-item {
  display: block;
  width: 120px;
  height: 25px;
  margin-bottom: 12px;
}
.g-ft .footer-content .ft-fl li .recruit-item a,
.g-ft .footer-content .ft-fr li .recruit-item a {
  display: inline-block;
}
.g-ft .footer-content .ft-fl li .recruit-item img,
.g-ft .footer-content .ft-fr li .recruit-item img {
  width: 25px;
  height: 25px;
}
.g-ft .footer-content .ft-fl li .ft-wechat-item,
.g-ft .footer-content .ft-fr li .ft-wechat-item {
  position: relative;
  height: 25px;
}
.g-ft .footer-content .ft-fl li .ft-wechat-item a,
.g-ft .footer-content .ft-fr li .ft-wechat-item a {
  float: left;
  margin-top: 0 !important;
}
.g-ft .footer-content .ft-fl li .ft-wechat-item img,
.g-ft .footer-content .ft-fr li .ft-wechat-item img {
  margin-left: 5px;
  margin-top: 5px;
  display: inline-block;
  width: 26px;
  height: 20px;
  float: left;
}
.g-ft .footer-content .ft-fl li .ft-wechat-item .ft-pop-service,
.g-ft .footer-content .ft-fr li .ft-wechat-item .ft-pop-service {
  position: absolute;
  display: none;
  top: -27px;
  left: 120px;
  width: 110px;
  height: 110px;
}
.g-ft .footer-content .ft-fl li .ft-wechat-item .ft-pop-service img,
.g-ft .footer-content .ft-fr li .ft-wechat-item .ft-pop-service img {
  width: 100%;
  height: 100%;
}
.g-ft .footer-content .ft-fl li .ft-wechat-item .ft-pop-public,
.g-ft .footer-content .ft-fr li .ft-wechat-item .ft-pop-public {
  position: absolute;
  display: none;
  top: -27px;
  left: 120px;
  width: 110px;
  height: 110px;
}
.g-ft .footer-content .ft-fl li .ft-wechat-item .ft-pop-public img,
.g-ft .footer-content .ft-fr li .ft-wechat-item .ft-pop-public img {
  width: 100%;
  height: 100%;
}
.g-ft .footer-content.bottom {
  border-bottom: none;
}
.g-ft .footer-content.bottom li {
  margin-top: 25px;
}
.g-ft .footer-content.bottom li a {
  padding-right: 45px;
  font-size: 18px;
  color: #c8caca;
}
.g-ft .footer-content.bottom h4 {
  margin-top: 40px;
  padding-bottom: 12px;
  font-weight: bold;
  font-size: 21px;
  color: #fff;
  display: inline-block;
  border-bottom: solid 2px #7e318e;
}
